@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: linear-gradient(#f9f9f9, #e0e0e0);
}
/* General Styles */
body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
}

h1, h2, h3 {
  color: #000000;
  font-style: normal;
  font-weight: normal;
  font-feature-settings: "smcp";
  font-variant: small-caps;
  letter-spacing: 0.6px;
}

h1 {
  font-size: 50px;
  text-align: left;
  line-height: 3px;
}

h2 {
  font-family: courier, "courier new", monospace;
  font-size: 25px;
  text-align: left;
  line-height: 2px;
}

h3 {
  font-family: courier, "courier new", monospace;
  font-size: 20px;
  text-align: left;
  line-height: 1.5px;
}

/* Flexbox Layouts */
.contact {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 20px;
}

@media (min-width: 600px) {
  .contact {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-left: 60px;
  }
}

.contact-info {
  font-family: "Playfair Display", sans-serif;
  display: flex;
  flex-direction: column;
}

@media (max-width: 600px) {
  .contact-info {
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
  }

  .contact-info h1, .contact-info h2, .contact-info h3 {
    word-wrap: break-word;
  }
}

.tables {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

@media (min-width: 600px) {
  .tables {
    flex-direction: row;
  }
}

.skills, .tools {
  flex: 1 1;
  margin: 10px;
  background-color: #f2f2f2;
  padding: 10px;
  border-radius: 5px;
  margin-top: 0px;
}

.skills table, .tools table {
  width: 100%;
  border-collapse: collapse;
}

.skills td, .tools td {
  border: 1px solid #ddd;
  padding: 8px;
}

.even-row {
  background-color: #d6e6e6;
}

.odd-row {
  background-color: #ffffff;
}

.download-button {
  font-family: Georgia, 'Times New Roman', Times, serif;
  display: inline-block;
  padding: 10px 20px;
  margin-top: 20px;
  background-color: black;
  color: white;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  border-radius: 5px;
  align-self: center;
}

.download-button:hover {
  background-color: #45a049;
}

.work-title {
  font-size: 40px;
  margin-top: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.work-exp {
  margin: 10px;
  padding: 10px;
  border-radius: 5px;
}

.work-item {
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
}

.company-name {
  font-family: "Poppins", sans-serif;
  font-size: 25px;
  margin-top: 10px;
  margin-bottom: 25px;
}

.job-title {
  font-family: "Poppins", sans-serif;
  font-size: 20px;
  margin: 5px 0;
}

.period {
  color: #4d4d4d;
  font-size: 20px;
  margin-top: 25px;
}

.job-description {
  width: 100%;
  font-size: 16px;
  margin-bottom: 10px;
}

.linkedin-logo,
.instagram-logo, 
.github-logo {
  width: 30px;
  height: 30px; 
  margin-right: 15px; 
}

.contact_actual {
  margin-left: 0px;
  text-align: center;
  font-size: 30px;
  border-bottom: 2px solid black;
  padding-bottom: 20px;
  margin-bottom: 0px;
}

.contact_links {
  margin-left: 85px;
  margin-bottom: 0px;
}

@media screen and (max-width: 600px) {
  h1 {
    font-size: 40px;
    line-height: 25px;
  }
  
  h2 {
    font-size: 20px;
    line-height: 40px;
  }
  
  h3 {
    font-size: 15px;
    line-height: 4px;
  }

  .contact-info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
  }
  
  .contact-info > * {
    margin: 5px 0;
    word-wrap: break-word;
  }

  .contact {
    flex-direction: column;
    align-items: flex-start;
  }

  .download-button {
    margin-top: 10px;
    align-self: flex-start;
  }

  .tables {
    flex-direction: column;
  }

  .work-item {
    display: flex;
    flex-direction: column;
  }
  
  .company-name, .period {
    display: block;
    line-height: normal;
    word-wrap: break-word;
  }

  .job-title {
    line-height: normal;
    word-wrap: break-word;
  }
}

nav {
  display: flex;
  justify-content: flex-start; 
  align-items: center;
  width: 100%;
  background-color: #f0f0f0; 
  padding: 10px;
  padding-left: 50px; 
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  position: relative;
  top: 20px;
  box-sizing: border-box; 
}

nav ul {
  display: flex;
  list-style-type: none;
  padding: 0;
  margin: 0;
}

nav li {
  margin: 0 15px;
}

nav li a {
  text-decoration: none;
  color: black;
}

nav li.active a {
  font-weight: bold;
  color: blue;
}

.content {
  width: calc(100% - 20px);
  padding: 10px;
  margin-top: 30px;
  box-sizing: border-box; 
}

/* Projects.css */

h1 {
    display: flex;
    align-items: center;
    justify-content: center;
}

.project-name, .class_name, .project-description {
    margin-left: 20px;
}

.school-item {
    padding-top: 1px;
    padding-bottom: 1px;
}

.project-name {
    font-size: 25px;
}

.personal-projects-title {
    margin-top: 50px;
    margin-bottom: 50px;
}

.project-description {
    margin-bottom: 10px;
}

.personal-item {
    padding-top: 1px;
    padding-bottom: 1px;
}

.personal-project-name {
    font-size: 25px;
    margin-left: 20px;
}

.personal-project-description {
    margin-left: 20px;
}

.personal-project-link {
    margin-left: 20px;
}

.even-row, .odd-row {
    margin-top: 20px;
}

.more-to-come-font {
    font-size: 40px; 
